.Post-wrapper {
  background: #f2f5fa;
  height: 100%;
}
.Post-body {
  padding-top: 0.1rem;
}
.post-header {
  height: 4.5rem;
  width: 100%;
}

.post-header h1{
  color: #fff;
  margin: 0;
}

.post-header p {
  color: #aaa;
}

.post-content {
  background: #fff;
  padding: 10px 180px 0;
  margin-bottom: 58px;
  font-size: 0.24rem;
  margin-top: -1.2rem;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  
}
.post-content img {
  max-width: 100%;
}

.pageList a{
  display: inline-block;
  color: #aaa;
  width: 50%;
  font-size: 0.2rem;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.pageList {
  padding: 1rem 0;
  justify-content: space-between;
}
.pageList span {
  padding: 0 20px;
}


div .mixMargin {
  margin-right: 0;
}

.returnback {
  text-align: center;
  margin-bottom: 20px;
}


.returnback a{
  display: inline-block;
  color: #fff;
  background: linear-gradient(to right,#00A5D7,#5AC8EB);
  padding: 0.2rem 0.3rem;
  border-radius: .1rem;
  font-size: 0.2rem;
}

.post-content a:active,.post-content a:visited {
  color: #aaa;
}

.title {
  font-weight: bold;
  padding-top: 20px;
  text-align: center
}

.credit_at {
  text-align: right;
}


.contBody {
  min-height: 600px;
}


#pageUp {
  float: left;
  display: flex;
}

#pageDown {
  float: right;
  display: flex;
  justify-content: flex-end;
}

#pageUp a:hover, #pageDown a:hover {
  color: #00A5D7;
}