/* .section .banner-section {
  padding-top: 1.2rem;
} */
.section {
  background-size: cover;
  background-repeat: no-repeat;

  .title {
    text-align: left;
  }
}

.bannerFirst {
  background-size: 100% 100%;
}

.bannerSecond {
  background-size: 100% 100%;
}

.bannerThird {
  height: 100%;
  background: url('./images/banner_03.png') no-repeat;
  background-size: 100% 100%;
}

.bannerFourth {
  background-size: 100% 100%;
}

.bannerFifth {
  background-size: 100% 100%;
}


/* Banner-section */

.banner-section {
  padding-top: 0.45rem;
}

.title h1 {
  font-size: 0.75rem;
  font-weight: 600;
  margin: 0 0 0.1rem 0;
}



@media screen and (max-width: 1350px) {
  .title h1 {
    font-size: 0.82rem;
  }
}

.title .sub-title {
  font-size: 0.6rem;
  font-weight: 600;
  margin-bottom: 0.1rem
}

.translation {
  padding-bottom: 0.25rem;
}
.translation>p {
  margin: 0;
  font-size: 0.28rem;
  color: #aaa;
}


.login-btn>a {
  position: relative;
  display: inline-block;
  width: 1.8rem;
  padding: 0.18rem 0;
  margin-bottom: 0.1rem;
  border-radius: 0.08rem;
  background: linear-gradient(to right,#00A5D7 , #5AC8EB);
  text-align: center;
  font-size: 0.25rem;
  color: #fff;
  cursor: pointer;
}
.login-btn>a:first-child {
  margin-right: 10px;
}

.xcx:hover:after {
  position: absolute;
  left: 0;
  top: 1rem;
  display: block;
  content: '';
  width: 100%;
  height: 1.8rem;
  background:url(../src/images/xiaochengxu.png) no-repeat;
  background-size: 100%;
  z-index: 100;
  box-shadow: 0 0 10px #ccc;
  border-radius: 5px;
  animation: fadeInDown 5s;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.bjnews:hover:after {
  position: absolute;
  left: 0;
  top: 1rem;
  display: block;
  content: '';
  width: 100%;
  height: 1.8rem;
  background:url(../src/images/gongzhonghao.png) no-repeat;
  background-size: 100%;
  z-index: 100;
  box-shadow: 0 0 10px #ccc;
  border-radius: 5px; 
  animation: fadeInDown 5s;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -20%, 0);
    transform: translate3d(0, -20%, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}



.feature {
  display: inline-block;
  padding-top: 0.4rem;
  margin-right: -2rem;
}

.feature ul {
  margin-left: -50px;
}

.feature ul li {
  display: inline-block;
  width: 30%;
  border-right: 1px solid #00A5D7;
  text-align: center;
  font-size: 20px;
  color: #00A5D7;
  // padding: 0 5px;
}
.feature ul li div {
  padding: 0 50px;
}

.feature ul li:last-child {
  border-right: 0;
}

.feature ul li p {
  margin:0;
  font-size: 0.20rem;
}


.feature ul li img {
  width: 55%;
  margin-bottom: 0.05rem;
  height: auto;
}

@media screen and (max-width: 1300px) {
  .feature ul li p {
    font-size: 0.16rem;
  }
}



.feature .ai,.feature .chainblock,.feature .service {
  width: 80PX;
  height: 80px;
  margin-bottom: 20px;
}



.section-right {
  float: left;
  width: 40%;
  padding-top: 0.2rem;
  margin-left: 100px;
}

.bannerFirst .section-left {
  padding-top: 0.7rem;
} 

@media screen and (max-width: 1350px) {
  .section-right {
    width: 40%;
    padding-top: 0.3rem;
  }
}

/* secondBanner */

.banner2 {
  .title h1 {
    font-size: 0.64rem;
  }
}
.experience:hover:after {
  position: absolute;
  left: 0;
  top: 1rem;
  display: block;
  content: '';
  width: 100%;
  height: 1.8rem;
  background: url(../src/images/xiaochengxu.png) no-repeat;
  background-size: 100%;
  z-index: 3;
  box-shadow: 0 0 10px #ccc;
  border-radius: 5px;
  animation: fadeInDown 5s;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.process ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.process ul li {
  display: inline-block;
  vertical-align: middle;
  width: 25%;
}

.process {
  width: 80%;
  padding-top: 0.3rem;
}

.process ul li p {
  height: 0.32rem;
  padding-top: 0.1rem;
  font-size: 0.2rem;
  color: #00A5D7;
  white-space: nowrap;
  text-align: center;
}
.process ul li.arrow-box {
  width: 5%;
  margin-top: -10px;
}



.process .arrow {
  width: 100%;
  height: 56px;
  background: url('../src/images/arrow.png') no-repeat;
  background-size: 100% 100%;
}

.bannerSecond .section-right {
  padding-top: 1rem;
}

@media screen and (max-width: 1350px) {
  .bannerSecond .section-right {
    padding-top: 0.5rem;
  }
}



/* bannerThird */

.bannerThird, .bannerFourth {
  text-align: center;
  position: relative;
}

.bannerThird-title-wrap {
  position: absolute;
  width: 100%;
  bottom: 65%;
}

.bannerThird-title {
  font-size: 0.68rem;
  padding-top: 0.3rem;
}


.bannerThird-sub-title {
  font-size: 0.36rem;
  color: #aaa;
}

.bannerThird .area {
  display: block;
  width: 13%;
  height: 17%;
}
.bannerThird .senior {
  position: absolute;
  bottom: 42%;
  left: 25%;
}
.bannerThird .abort {
  position: absolute;
  bottom: 17%;
  left: 20%;
}
.bannerThird .undergraduate {
  position: absolute;
  bottom: 40%;
  left: 64%;
}

.bannerThird .master {
  position: absolute;
  bottom: 15%;
  left: 65%;
}

.bannerThird .degree {
  position: absolute;
  background: url(./images/entranceservices.png) no-repeat;
  background-size: 100% 100%;
  bottom: 0;
  width: 100%;
  height: 60%;

  @media (max-width: 980px) {
    height: 25%;
  }
}

/* bannerFourth */

div .strategy-list {
  padding-top: 0.35rem;
  padding-left: 40px;
}
.strategy-list .strategy-item {
  margin-right: 0.35rem;
  padding: 0;
  border-radius: 10px;
  text-align: left;
  box-shadow: 0px 5px 20px #ccc;
  background: #fff;
} 


.strategy-item img {
  min-height: 3.3rem;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

@media screen and (max-width: 1300px) {
  .strategy-item img {
    min-height: 2.6rem;
  }
}

.strategy-item-bottom {
  padding:0 0.25rem;
  margin: 0;
}


.strategy-item h3 {
  font-size: 30px;
}

.strategy-item p {
  font-size: 0.18rem;
  height: 0.6rem;
  color: #aaa;
  padding-bottom: 0.2rem;
  overflow: hidden;
  text-overflow: ellipsis;
}


.more {
  width: 0.8rem;
  height: 0.8rem;
  margin-top: 1.5rem;
  background: url('../src/images/more.png') no-repeat;
  background-size: 100%;
  color: #00A5D7;
  font-size: 0.18rem;
  line-height: 2rem;
  white-space: nowrap;
}

.strategy-item-bottom h3 {
  margin-top: 0.2rem;
  margin-bottom: 0.1rem;
}



/* bannerFivth */

.banner5 {
  .title h1 {
    font-size: 0.82rem;
    white-space: nowrap;
  }

  .title .sub-title {
    white-space: nowrap;
  }
}

.qrcode-list {
  display: inline-block;
  width: 100%;
  max-width: 5rem;
  padding: 0.4rem 0.2rem 0.3rem 0;
}

@media screen and (max-width: 1300px) {
  .qrcode-list {
    padding: 20px 10px;
  }

}

.qrcode-item {
  display: inline-block;
  width: 38%;
  margin-right: 0.3rem;
} 
.qrcode-item p {
  text-align: center;
  font-size: 0.24rem;
  color: #00A5D7;
}

.qrcode-list img {
  width: 100%;
  height: auto;
  padding: 10px;
  background: #fff;
  border: 1px solid #ccc;
}

.careers {
  padding-top: 1.2rem;
}

.xiaomeng-rtk {
  display: flex;
  border-radius: 10px;
  background: #fff;
  max-width: 4.8rem;
  border: 1px solid #00A5D7;
  border-left: 0;
}

.rtk-title {
  background: linear-gradient(to right, #00A5D7, #5AC8EB);
  color: #fff;
  font-size: 0.26rem;
  padding: 0.32rem;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}


.rtk-content {
  padding-left:0.2rem;
  padding-top: 0.2rem;
  font-size: 0.22rem;
  max-width: 3.6rem;

  p {
    white-space: nowrap;
    overflow:hidden;
    text-overflow: ellipsis;
    cursor: pointer; 
  }
  p:hover {
    color: #00A5D7;
  }
}

.toLink a {
  color: #00A5D7
}

.toLink a:hover{
  color: #00A5D7;
}






@media screen and ( max-width: 1600px) and (min-width:1350px) {
  .title h1 {
    font-size: 0.82rem
  }
  .title .sub-title {
    font-size: 0.64rem;
  }

  .translation>p {
    font-size: 0.3rem;
  }
  div .strategy-item h3 {
    font-size: 0.24rem;
  }

  .strategy-item p {
    font-size: 17px;
  }

  .bannerThird-title {
    padding-bottom: 0;
  }

  .process .arrow {
    height: 46px;
    margin-top: -30px;
  }
}

@media screen and (max-width: 1350px) {
  .bannerThird-title {
    font-size: 0.6rem;
    padding: 0;
  }
  .bannerThird-sub-title {
    font-size: 0.28rem;
  }
  div .strategy-item h3 {
    font-size: 0.22rem;
  }
  div .strategy-item p {
    padding-bottom: 0;
    font-size: 0.16rem;
  }

  .translation>p {
    font-size: 0.3rem;
  }

  .process .arrow {
    height: 36px;
    margin-top: -30px;
  }
}

@media screen and (min-width: 1800px) {
  .bannerThird-title {
    padding-top: 0.6rem
  }
} 



/*  */

@media screen and (min-width: 768px) and (max-width: 992px) {
  .section-left {
    text-align: center;
  }
  .feature ul {
    margin-left: 0;
  }
  .section-right {
    text-align: center;
  }

  .xiaomeng-rtk {
    display: none;
  }

  .bannerFifth .section-right {
    margin-left: 30%;
  }
  .bannerFifth .section-right img{
    padding-top: 0;
  }
  .strategy-item img {
    min-height: 3.3rem;
  }

  .process {
    width: 100%;
  }
}

/*  */

@media screen and (max-width: 1000px) {
  .rtk-content {
    padding-left: 0;
  }
}