.carousel {
  height: 100%;
}

.carousel-inner {
  height: 100%;
}

.carousel-inner .item {
  height: 100%;
}


li .nav-btn-active {
  color: #00A5D7;
}

li .nav-btn-active:after {
  position: absolute;
  display: block;
  bottom: 0.02rem;
  content: "";
  width: 100%;
  height: 4px;
  background: #00A5D7;
}

.navlist-btm-items a:hover ,.navlist-btm-items a:focus {
  color: #00A5D7;
}

.LenUkDetail-wrapper {
  height: 100%;
}

.LenUkDetail-body {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 0.8rem 0 1rem;
}

div .navlist-btm {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 1rem;
  line-height: 1rem;
  background-color: #f2f5fa;
  margin: 0;
}

.navlist-btm-items > li  {
  display: inline-block;
} 


.navlist-btm-items {
  text-align: center;
  font-size: 0.3rem;
  margin: 0;
}

.navlist-btm-items li {
  position: relative;
}

.navlist-btm-items li:hover :after {
  position: absolute;
  display: block;
  bottom: 2px;
  content: "";
  width: 100%;
  height: 4px;
  background: #00A5D7;
}

.navlist-btm-items  a {
  display: block;
  width: 100%;
  height: 100%;
  color: #191722;
}

.navlist-btm-items li:hover a,.navlist-btm-items  a:active {
  color: #00A5D7;
}
