.Notifies-wrapper {
  transition: all .35s linear;
  position: fixed;
  text-align: center;
  color: #fff;
  width: 100%;
  height: 1.4rem;
  line-height: 1.5rem;
  background: #00A5D7;
  font-size: 0.38rem;
  z-index: 10000;
}

.noti-logo {
  display: inline-block;
  height: 50%;
  vertical-align: middle;
  padding-right: 0.2rem;
  margin-right: 0.2rem;
  border-right: 2px solid #fff;
}


.noti-logo img {
  width: auto;
  height: 100%;
  vertical-align: top;
}

.Notifies-wrapper-abnor {
  height: 0.3rem;
  line-height: 0.3rem;
  font-size: 0.16rem;
}