.personal-wrapper {
  width: 100%;
  background: #f2f5fa;
  padding-top: 100px;
  color: #191722;

  .containerWrap {
    width: 92%;
    margin: 0 auto;
  }

  .account {
    display: flex;
    align-items: center;
    height: 2.4rem;
    padding: 0 0.5rem;
    font-family: "Segoe UI";
    background: #fff;
    border-radius: 0.1rem;

    .accountLeft {
      width: 1.5rem;
      img {
        border-radius: 50%;
        width: 1.2rem;
        height: 1.2rem;
      }
    }

    .accountRight {
      margin-left: 0.2rem;
    }

    .name {
      font-size: 0.3rem;
      word-break: break-all;
    }

    .iden {
      font-size: 0.15rem;
      color: #aaa;
    }
  }

  .detail {
    background: #fff;
    border-radius: 0.1rem;
    text-align: center;
  }

  .token,
  .invite {
    margin: 0.44rem -15px;
  }

  .token {
    border-right: 1px solid #00a5d7;
  }

  .tokenTitle,
  .inviteTitle {
    font-size: 0.25rem;
  }

  .toeknNum,
  .inviteNum {
    font-size: 0.6rem;
  }

  .qrcode_wrap {
    min-height: 100%;
    position: absolute;
    width: 100%;
    background: #f2f5fa;
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      width: 3.5rem;
    }
  }

  .qr_text {
    text-align: center;
    margin-top: 0.1rem;
  }

  .layout {
    cursor: pointer;
    color: #00a5d7;
  }

  .loading {
    font-size: 10px;
    margin: 50px auto;
    text-indent: -9999em;
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    background: #00a5d7;
    background: -moz-linear-gradient(left, #00a5d7 10%, rgba(255, 255, 255, 0) 42%);
    background: -webkit-linear-gradient(left, #00a5d7 10%, rgba(255, 255, 255, 0) 42%);
    background: -o-linear-gradient(left, #00a5d7 10%, rgba(255, 255, 255, 0) 42%);
    background: -ms-linear-gradient(left, #00a5d7 10%, rgba(255, 255, 255, 0) 42%);
    background: linear-gradient(to right, #00a5d7 10%, rgba(255, 255, 255, 0) 42%);
    position: relative;
    -webkit-animation: load3 1.4s infinite linear;
    animation: load3 1.4s infinite linear;
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
  }

  .loading:after {
    background: #f2f5fa;
    width: 75%;
    height: 75%;
    border-radius: 50%;
    content: "";
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  }

  @keyframes load3 {
    0% {
      /* -webkit-transform: rotate(0deg); */
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }

  
}
