.nav-bar-wrapper-fixed {
  position: fixed !important;
}

.nav-bar-wrapper {
  position: absolute;
  z-index: 1000;
  width: 100%;
  top: 0;

  .lang {
    margin-left: 0.2rem;
    span {
      color: #00A5D7;
      opacity: 0.4;
      cursor: pointer;
    }

    .langopacity {
      color: #00A5D7;
      opacity: 1;
    }
  }

  

  .separator {
    display: inline-block;
    width: 2px;
    height: 0.2rem;
    margin: 0 0.1rem;
    background: #00A5D7;
  }
}
.row .nav-bar {

  display: flex;
  justify-content:space-between;
  align-items: center;
}

.logo {
  float: left;
  width: 1.8rem;
  height: 0.7rem;
  padding-top: 0.1rem;
  background-size: 100% 100%;
}

.nav-bar-wrapper .avator {
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
}

.nav-bar-menu {
  display: inline-block;
  height: 0.8rem;
}

.nav-bar-menu>ul {
  overflow: hidden;
}

.nav-bar-menu>ul>li {
  float: left;
  font-size: 0.26rem;
  line-height: 0.8rem;
}

@media screen and (max-width: 768px) {
  .nav-bar-menu>ul>li {
    font-size: 0.16rem;
  }
}
.nav-bar-menu>ul>li a {
  display: block;
  color: #191722;
  text-align: center; 
  padding: 0 0.2rem;
}
.nav-bar-menu>ul>li a:hover{
  text-decoration: none;
}

.nav-bar .active {
  color: #00A5D7;
  border-bottom: 2px solid #00A5D7;
}




