/* common */

.bannder-img-item {
  position: relative;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: 0px 0px;  
  background-repeat: no-repeat;
}


.bannder-img-item .caption {
  padding: 0.25rem;
  width: 25%;
  height: auto;
  font-size: 0.24rem;
  line-height: 0.5rem;
  color: #fff;
  background: rgba(0, 0, 0, 0.5);
}

.bannder-img-item .middle {
  width: 30%;
}
.bannder-img-item .big {
  width: 40%;
}
.bannder-img-item .caption p {
  text-indent: 0.45rem;
}

.bannder-img-item .caption h2 {
  font-size: 0.35rem;
  font-weight: bold;
  color: #fff;
}

.bannder-img-item .onLeft {
  position: absolute;
  top: 1rem;
  left: 2rem;
}

.bannder-img-item .onRight {
  position: absolute;
  top: 1rem;
  right: 2rem;
}


