div .strategy-detail-wrapper {
  background: #f2f5fa;
  width: 100%;
  height: 100%;
  margin-left: 0;
  margin-right: 0;
}

.strategy-detail-content a:hover {
  color:#00A5D7;
}

.strategy-detail-content>a {
  display: block;
}

.strategy-detail-body {
  position: relative;
  padding-top: 1rem;
}

.content-block {
  background: #fff;
}

.category-list {
  margin-top: 0.5rem;
}
.category-list>li {
  margin-bottom: 20px;
}
.category-list>li:hover {
  background: linear-gradient(to right,#00A5D7 , #5AC8EB);
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;  
}

.category-list li  a{
  display: inline-block;
  width: 100%;
  padding:0.1rem 0.5rem 0.1rem 0;
  text-align: right;
  font-size: 0.32rem;
  color: #00A5D7;
} 

.category-list li  a:hover {
  color: #fff;
}


.category-unit-active {
  background: linear-gradient(to right,#00A5D7 , #5AC8EB);
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;  
}
.category-list .category-unit-active a {
  color: #fff;
}



.bulb-icon {
  display: inline-block;
  width: 50px;
  height: 50px;
  background: url('../../images/bulb.png') no-repeat;
  background-size: 100%;
  vertical-align: bottom;
  margin-right: 20px;
}

.post-num {
  display: inline-block;
  border-radius: 50%;
  background: #00A5D7;
  color: #fff;
  text-align: center;
  line-height: 40px;
  width: 40px;
  height: 40px; 
  font-size: 0.2rem;
  font-weight: 600;
  margin-right: 10px;
}

.content-block {
  padding-top: 0.5rem;
  padding-left: 0.3rem;
  padding-right: 1rem;
  padding-bottom: 0.3rem;
  margin-bottom: 0.2rem;
}

.content-block h2{
 margin-top: 0;
 padding-top: 0.3rem;
 font-size: 0.5rem; 
 color: #191722;
}

.qa-list {
  padding-top: 0.2rem;
  padding-left: 0.5rem;
}

.qa-list h3 {
  font-size: 0.32rem;
  color: #191722;
  font-weight: 600;
}

.qa-list h4 {
  margin-left: 0.4rem;
  padding: 0.2rem 0;
  font-size: 0.28rem;
  color: #00A5D7;
  font-weight: 600;
  line-height: 0.45rem;
}

.qa-list p {
  padding-left: 0.4rem;
  font-size: 0.24rem;
  line-height: 0.5rem;
  text-indent: 0.5rem;
  color: #191722;
}


.qa-list-item {
  margin-bottom: 0.3rem;
}

.nav-list {
  float: none;
  width: 4rem;
}

.strategy-detail-content {
  margin-left: 4rem;
}

.tips {
  padding-top: 0.2rem;
  padding-left: 0.6rem;
}

.tips li{
  overflow: hidden;
}

.tips li p,.tips li span {
  float: left;
  vertical-align: top;
  padding: 0;
  text-indent: 0;
}
.tips li span {
  width: 0.5rem;
  height: 0.5rem;
  margin-right: 0.2rem;
  background: #00A5D7;
  color: #f2f5fa;
  font-size: 0.24rem;
  border-radius: 50%;
  line-height: 50px;
  text-align: center;
}

.tips li p {
  width: 80%;
}

.time-plain-item {
  overflow: hidden;
}

.time-plain {
  padding-top: 0.3rem;
  padding-left: 0.6rem;
}

.mon-gup {
  position: relative;
  float: left;
  width: 15%;
  padding: 0.1rem 0.2rem 0.1rem 0; 
  text-align: right;
  font-size: 0.24rem;
  font-weight: 600;
  line-height: 0.5rem;
  white-space: nowrap;
}
.mon-gup:after{
  position: absolute;
  right: -0.11rem;
  top: 35%;
  display: block;
  content: "";
  width: 0.2rem;
  height: 0.2rem;
  border-radius: 50%;
  background: #00A5D7;
}

.time-plain-item p {
  float: left;
  padding: 0.1rem 0 0.1rem 0.4rem;
  width: 80%;
  word-break: break-all;
  text-indent: 0;
  margin: 0;
  border-left: 2px solid #00A5D7;
}

.exam-content p {
  text-indent: 0;
}

.ques-type p {
  text-indent: 0;
}

.basicMaterial {
  padding-top: 0.2rem;
}
.basicMateril-item {
  display: inline-block;
  vertical-align: top;
  width: 20%;
  padding: 0 0.3rem 0.3rem;
  text-align: center;
  font-size: 0.22rem;
  color: #191722;
}
.basicMateril-item img {
  margin-bottom: 0.1rem;
}


.checklist p {
  text-indent: 0;
}

@media screen and ( max-width: 1300px) {
  .basicMateril-item {
    font-size: 0.16rem;
  }
} 