.downloadBody {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #F2F5FA;
  padding-top: 100px;
  overflow-x: hidden;


  .downloadList {
    background: #fff;
    padding: 0.6rem;
  }

  table {
    width: 100%;
    th {
      padding: 0.3rem 0;
      font-size: 0.25rem;
      font-weight: bold;
    }

    td {
      font-size: 0.25rem;
      margin-bottom: 0.3rem;
      vertical-align:middle;
    }
  }

  .menuTop {
    overflow: hidden;
  }

  h2 {
    float: left;
    font-size: 0.36rem;
    margin-top: 0.1rem;
  }

  .all_download {
    float: right;
    display: block;
    width: 1.6rem;
    height: 0.6rem;
    color: #fff;
    background: linear-gradient(to bottom right, #00A5D7, #5AC8EB);
    text-align: center;
    line-height: 0.6rem;
    border-radius: 0.1rem;
    font-size: 0.25rem;
    cursor: pointer;
  }

  .download {
    margin-top: 0.1rem;
    display: inline-block;
    width: 0.3rem;
    height: 0.3rem;
    background: url(../../images/personal/download.png) no-repeat;
    background-size: 100% 100%;
  }


  .sideTop {
    background: #00A5D7;
    padding: 0.2rem 0.3rem;
    color: #fff;
    border-radius: 0.1rem;
    margin-bottom: 0.3rem;
    h3 {
      font-size: 0.28rem;
      font-weight: 600;
      color: #fff;
    }

    p {
      font-size: 0.18rem;
    }
  }

  .sideBtm {
    text-indent: 30px;
    background: #fff;
    font-size: 0.15rem;
    padding: 0.4rem 0.3rem;
    border-bottom-left-radius: 0.1rem;
    border-bottom-right-radius: 0.1rem;
    height: 6rem;
  }
}