.tabMenuWrapper {
  padding-top: 0.2rem;

  //

  .closeWrap {
    text-align: center;
    font-size: 0.225rem;
    font-weight: bold;

    button {
      background: linear-gradient(to right bottom, #00a5d7, #5ac8eb);
      width: 1.8rem;
      height: 0.5rem;
      color: #fff;
      font-weight: 500;
      border: 0;
      border-radius: 0.04rem;
    }
  }

  .tabCard {
    background: #c8e6f0;
    border-radius: 0.1rem 0.1rem 0 0;
    height: 0.7rem;
    line-height: 0.7rem;
    text-align: center;
    color: #00a5d7;
    font-size: 0.25rem;
    cursor: pointer;
  }

  .active {
    background: #fff;
  }

  .pannel {
    display: none;
    width: 100%;
    background: #fff;
    padding: 0.6rem 0.5rem;  
  }

  .npannel {
    padding: 0.6rem 0.1rem;
  }


  .pannelActive {
    display: block;
  }

  .processTitle {
    font-size: 0.3rem;
    font-weight: bold;
    padding-top: 0.1rem;
    margin-bottom: 0.1rem;
  }

  .processDesc {
    font-size: 0.2rem;
    color: #aaa;
    padding-bottom: 0.2rem;
  }

  .processDesc,
  .processTitle {
    text-align: center;
    padding-left: 0.75rem;
  }

  // steps

  .top {
    padding-bottom: 40px;
  }

  .rmc-steps-item-finish .rmc-steps-item-icon {
    position: relative;
    width: 20px;
    height: 20px;
    background: url(../../images/personal/stepFinish.png) no-repeat;
    background-size: 100% 100%;
    z-index: 1;
    // box-shadow: 0 0 0 3px #00a5d7;
  }

  .rmc-steps-label-vertical .rmc-steps-item-tail {
    padding: 0;
    margin-left: 45px;
    top: 8px;
    z-index: 0;
  }

  .rmc-steps-item-finish .rmc-steps-item-tail:after {
    background: #00a5d7;
  }

  .rmc-steps-item-tail:after {
    height: 4px;
  }

  .rmc-steps-item-icon > .rmc-steps-icon {
    display: none;
  }

  .rmc-steps-item-icon {
    width: 9px;
    height: 9px;
    background: #00a5d7;
    border: 0;
  }

  .rmc-steps-item-process .rmc-steps-item-icon {
    position: relative;
    z-index: 1;
    width: 9px;
    height: 9px;
    border-radius: 50%;
    background: #fff;
    box-shadow: 0 0 0 4px #00a5d7;
  }

  .rmc-steps-item-process,
  .rmc-steps-item-wait {
    padding-top: 5px;
  }

  .rmc-steps-item-title {
    font-size: 12px;
  }

  .rmc-steps-label-vertical .rmc-steps-item-content {
    width: auto;
    padding: 0 0.1rem;
  }

  .rmc-steps-item-process  {
    padding-top: 3px;
  }

  .rmc-steps-item-process .rmc-steps-item-icon {
    margin-top: 4px;
    margin-bottom: 4px;
  }

  // 
  .personalInfo {
    .formTitle {
      font-size: 0.25rem;
      padding-bottom: 0.25rem;
    }

    .Registration,
    .personalInfo {
      border: 1px solid #00a5d7;
      padding: 0.3rem 0.5rem;
      margin-bottom: 0.5rem;
    }

    label {
      font-size: 0.25rem;
      font-weight: normal;
      margin-right: 0.5rem;
      width: 1rem;
    }

    .inputGroup {
      position: relative;
      margin-bottom: 0.3rem;
    }

    input[type="text"],
    input[type="email"] {
      width: 5.7rem;
      height: 0.5rem;
      padding-left: 0.2rem;
      border: 1px solid #a0e1f5;
      outline: none;
    }

    .inputState {
      display: inline-block;
      vertical-align: middle;
      margin-left: 0.2rem;
      width: 0.4rem;
      height: 0.4rem;
      background: url(../../images/personal/success.png) no-repeat;
      background-size: 100% 100%;
    }

    .userIcon {
      width: 0.8rem;
      height: 0.8rem;
    }

    .wx {
      font-size: 0.25rem;
      color: #00a5d7;
    }

    .ant-select-selection {
      border-color: #a0e1f5;
      color: #00a5d7;
      border-radius: 0;
      height: 0.5rem;
    }

    .ant-select-selection__rendered {
      line-height: 0.5rem;
    }

    .ant-select-arrow .ant-select-arrow-icon svg {
      color: #00a5d7;
    }

    .uploadItem {
      position: relative;
      padding: 0.2rem 0.3rem;
      margin-bottom: 0.2rem;
      background: #f2f5fa;
      min-height: 2rem;
      .left {
        display: inline-block;
        width: 75%;
        padding-right: 0.1rem;
        vertical-align: middle;

        p {
          word-wrap:break-word;
          word-break: break-all;
        }
        p:nth-child(1) {
          font-size: 12px;
        }
      }
      .right {
        position: relative;
        display: inline-block;
        width: 25%;
        vertical-align: middle;
        cursor: pointer;
      }

      input[type="file"] {
        position: absolute;
        opacity: 0;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        cursor: pointer;
        z-index: 1;
      }

      h3 {
        font-size: 0.32rem;
        padding-bottom: 0.25rem;
      }

      .state {
        position: absolute;
        top: 0;
        right: 0;
        width: 0.5rem;
        height: 0.5rem;
        background: url(../../images/personal/stateSuccess.png) no-repeat;
        background-size: 100%;
      }
    }

    .save {
      width: 2.2rem;
      height: 0.8rem;
      border: 0;
      border-radius: 0.1rem;
      background: linear-gradient(to bottom right, #00a5d7, #5ac8eb);
      color: #fff;
      outline: 0;
      font-size: 0.24rem;
    }
  }

  // 

  .courseCenter {
    .groupTitle {
      font-size: 0.25rem;
      padding-bottom: 0.3rem;
    }

    .myCourse,
    .moreCourse {
      border: 1px solid #00a5d7;
      padding: 0.5rem;
      margin-bottom: 0.3rem;
    }

    .noSub {
      text-align: center;
      font-size: 0.3rem;
      padding: 1.8rem 0;
    }

    .courseItem,
    .Subscribed {
      background: #f2f5fa;
      border-radius: 0.1rem;
      margin-bottom: 0.4rem;

      img {
        height: 4rem;
        border-top-left-radius: 0.1rem;
        border-top-right-radius: 0.1rem;
      }
    }

    .courseItem img {
      height: 3.5rem;
    }

    .courseName {
      display: inline-block;
      font-size: 0.3rem;
      margin-top: 0.1rem;
    }

    .courseBtm {
      padding: 0.3rem 0.4rem;
      overflow: hidden;
    }

    .details,
    .download {
      float: right;
      display: block;
      width: 1.6rem;
      height: 0.6rem;
      background: linear-gradient(to right bottom, #00a5d7, #5ac8eb);
      color: #fff;
      line-height: 0.6rem;
      text-align: center;
      border-radius: 0.04rem;
      font-size: 0.24rem;
      font-weight: bold;
      border: 0;
    }
  }

  .shade {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: transparent;
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: opacity 0.5s;
    opacity: 1;
  }

  .courseDetail {
    margin-top: 0.5rem;
    width: 7rem;
    box-shadow: 0 0 25px #ccc;

    img {
      height: 3.2rem;
    }

    .main {
      padding: 0.2rem;
      background: #fff;
      text-align: left;
    }

    h2 {
      margin: 0 0 0.1rem;
    }

    .prov {
      color: #aaa;
      font-size: 0.2rem;
    }

    .desc {
      font-size: 14px;
      text-indent: 28px;
    }
  }

  .hidden {
    display: none;
    opacity: 0;
  }

  // 
  .myToken {
    .headline {
      font-size: 0.25rem;
      font-weight: bold;
      padding-bottom: 0.2rem;
    }
  }

  .tokenWrap {
    display: flex;
    border: 1px solid #00a5d7;
    margin-bottom: 0.3rem;
    justify-content: stretch;
  }

  .myTokenWrap {
    height: 5rem;
  }

  .rules {
    padding: 0.2rem 0.1rem;
    height: 100%;
    li {
      padding-right: 0.1rem;
      margin-bottom: 0.15rem;
    }

    h4 {
      padding: 0 0.2rem 0.1rem;
      font-size: 0.25rem;
      font-weight: 600;
    }
  }

  .dataItem {
    width: 24%;
    background: linear-gradient(to bottom right, #00a5d7, #5ac8eb);
    color: #fff;
    text-align: center;
    position: relative;

    .itemTitle {
      margin-top: 1.2rem;
      font-size: 0.25rem;
    }

    .itemDate {
      font-size: 0.45rem;
    }

    .itemMore {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      margin: 0 auto 0.5rem;
      cursor: pointer;
    }
  }

  .shop {
    padding: 0.5rem;
  }

  .shopItemBtm {
    background: #f2f5fa;
    padding: 0.25rem 0.25rem;
    border-bottom-left-radius: 0.1rem;
    border-bottom-right-radius: 0.1rem;
    overflow: hidden;
    font-size: 0.3rem;

    p {
      float: left;
      padding: 0.1rem 0 0;
      margin-bottom: 0;
    }

    button {
      float: right;
      width: 1.2rem;
      height: 0.6rem;
      background: linear-gradient(to bottom right, #00a5d7, #5ac8eb);
      color: #fff;
      border: 0;
      border-radius: 8px;
    }

    .expect_btn {
      width: 1.5rem;
    }
  }

  // 

  .tokenHistory {
    width: 6.8rem;
    box-shadow: 0 0 25px #ccc;

    .title {
      padding: 0.2rem 0.4rem;
      background: linear-gradient(to right bottom, #00a5d7, #5ac8eb);
      font-size: 0.3rem;
      color: #fff;
      text-align: left;
      border-top-left-radius: 0.1rem;
      border-top-right-radius: 0.1rem;
    }

    .main {
      background: #fff;
      padding: 0.2rem 0.3rem;
      font-size: 0.25rem;
      border: 1px solid #00a5d7;
      border-top: 0;
      border-bottom-left-radius: 0.1rem;
      border-bottom-right-radius: 0.1rem;
    }

    .scrollWrap {
      height: 2rem;
      overflow-y: auto;
      padding-right: 8px;
      margin-bottom: 0.2rem;

      &::-webkit-scrollbar {
        width: 10px;
      }
      &::-webkit-scrollbar-thumb {
        border-radius: 5px;
        background: #00a5d7;
      }
      &::-webkit-scrollbar-track {
        border-radius: 5px;
        background-color: #eee;
      }
    }

    table {
      width: 100%;
      text-align: right;

      td:nth-child(1) {
        text-align: left;
      }

      td {
        padding: 0 10px 0 0;
        font-size: 14px;
        font-size: 16px;
      }
    }
  }

  // 
  .myInvite {
    width: 6rem;
    box-shadow: 0 0 25px #ccc;

    .title {
      padding: 0.2rem 0.4rem;
      background: linear-gradient(to right bottom, #00a5d7, #5ac8eb);
      font-size: 0.3rem;
      color: #fff;
      text-align: left;
      border-top-left-radius: 0.1rem;
      border-top-right-radius: 0.1rem;
    }

    .main {
      background: #fff;
      padding: 0.2rem 0.4rem;
      font-size: 0.25rem;
      border: 1px solid #00a5d7;
      border-top: 0;
      border-bottom-left-radius: 0.1rem;
      border-bottom-right-radius: 0.1rem;
    }
    .groupWrap {
      display: flex;
      margin-bottom: 0.2rem;
      span {
        width: 1.4rem;
        margin-top: 0.1rem;
        font-size: 0.2rem;
      }
      input {
        padding-left: 7px;
      }
    }
    input[type="text"] {
      flex: auto;
      border: 1px solid #00a5d7;
    }

    .inviteList {
      width: 302px;
      flex: auto;
      display: inline-block;
      border: 1px solid #00a5d7;
      height: 2rem;
    }

    .tips {
      color: #00a5d7;
      padding-top: 0;
      font-size: 0.16rem;
      padding-left: 1.4rem;
    }
  }

  // 

  .tokenDeposit {
    width: 6rem;
    text-align: center;
    box-shadow: 0 0 25px #ccc;

    .title {
      font-size: 0.32rem;
    }

    .rule {
      color: #aaa;
      font-size: 0.16rem;
    }

    .main {
      padding: 0.2rem 0;
      background: #fff;
      border-bottom-left-radius: 0.1rem;
      border-bottom-right-radius: 0.1rem;
    }

    .depositWrap {
      display: inline-block;
      width: 3rem;
    }

    .depositTop {
      padding: 0.1rem 0 1px;
      background: linear-gradient(to right bottom, #00a5d7, #5ac8eb);
      border-top-left-radius: 0.1rem;
      border-top-right-radius: 0.1rem;
      color: #fff;

      .strong {
        font-size: 0.32rem;
      }
    }

    .depositBtm {
      background: #fff;
      border: 1px solid #00a5d7;
      border-top: 0;
      border-bottom-left-radius: 0.1rem;
      border-bottom-right-radius: 0.1rem;
      padding: 0.1rem 0.3rem;

      .depositMonBtm {
        display: flex;


        .left, .right {
          font-size: 30px;
          color: #00a5d7;
          font-weight: 700;
          cursor: pointer;
        }
      }
    }

    input[type="text"] {
      width: 100%;
      border: 0;
      text-align: center;
      font-size: 0.3rem;
      color: #00a5d7;
      outline: none;
      background: transparent;
    }

    .note {
      color: #00a5d7;
      padding: 0.1rem;
    }

    button {
      background: #00a5d7 linear-gradient(to right bottom, #00a5d7, #5ac8eb);
      color: #fff;
      width: 1.6rem;
      height: 0.5rem;
      border-radius: 4px;
      border: 0;

      &:first-child {
        margin-right: 0.1rem;
      }
    }
  }

  .paging {
    width: 100%;
    padding: 10px 0;
    overflow: hidden;
    display: flex;
    justify-content: flex-end;
    button {
      border: 0;
      font-size: 14px;
      width: 0.8rem;
      height: 0.3rem;
      margin-right: 0.1rem;
      color: #fff;
      background: #00a5d7 linear-gradient(to right bottom, #00a5d7, #5ac8eb);
    }
  }

  .inviteItem {
    text-align: center;
    display: inline-block;
    margin: 0.1rem 0.2rem 0.2rem;

    p {
      width: 0.8rem;
      font-size: 0.2rem;
      word-break: break-all;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }

  .invite_avator {
    width: 0.5rem;
    height: 0.5rem;
    
    img {
      width: 100%;
      border-radius: 50%;
    }
  }

  .creditMission {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 9999;
    background-color: rgba(67,67,67,0.9);
    text-align: center;

    .creditBox {
      width: 5rem;
      position: absolute;
    }

    .creditTitle {
      padding-top: 20px;
      color: #f4d242;
      font-size: 24px;
    }

    .creditmark {
      color: #f4d242;
      font-size: 30px;
      font-weight: bold;
    }
  }
}
