.AbroadStrategy-wrapper {
  position: absolute;
  background: #f2f5fa;
  width: 100%;
  min-height: 100%;
}

.AbroadStrategy-top-body-left {
  padding-top: 0.4rem;
}
.AbroadStrategy-top {
  padding-top: 0.1rem;
}

.AbroadStrategy-top h1 {
  padding-top: 1.5rem;
  font-size: 0.56rem;
  font-weight: bold;
}

.AbroadStrategy-top p {
  font-size: 0.5rem;
  font-weight: bold;
} 


.btn-list>a{
  position: relative;
  display: inline-block;
  font-size: 0.28rem;
  padding: 0.14rem 0.2rem;
  border-radius: 0.08rem;
  color: #fff;
  background: linear-gradient(to right,#00A5D7 , #5AC8EB);
}
.btn-list>a:first-child {
  margin-right: 0.1rem;
}


/* startegy-list */

.AbroadStrategy-bottom {
  padding-top: 0.3rem;

  h2 {
    margin-top: 0.2rem;
  }
}
.strategy-post-item {
  background: #fff;
  margin-bottom: 30px;
}  

.strategy-post-item .item-head {
  padding: 0;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.item-head h2, .item-head p{
  padding: 0 20px;
  text-align: right;
  color:#fff;
}

.item-head h2 {
  padding-top: 25px;
  font-size: 48px;
}

.item-head p {
  font-size: 16px;
}

/* .strategy-post-item .oversea {
  height: 250px;
  background: url(../../images/strategy/oversea.png) no-repeat 0 -20px;
  background-size: cover;  
} */

.post-link-list {
  text-align: center;
}

.post-link-list a {
  display: inline-block;
  padding: 20px 0; 
  color: #191722;
  font-size: 28px;
}

.post-link-list a:hover {
  color: #00A5D7;
}

.strategy-more {
  display: block;
  width: 64px;
  height: 64px;
  margin-top: 93px;
  background: url(../../images/more.png) no-repeat;
  background-size: 100%;
}

.AbroadStrategy-bottom .fee {
  background: url(../../images/service/fee.png) no-repeat;
  background-size: contain;
}

.AbroadStrategy-bottom .trip {
  background: url(../../images/service/trip.png) no-repeat;
  background-size: contain;
}

.AbroadStrategy-bottom .accommodation {
  background: url(../../images/service/accommodation.png) no-repeat;
  background-size: contain;
}


.EntranceService-bottom a {
  width: 100%;
  height: 100%;

  h2 {
    font-weight: 600;
  }
}


/* hidden */

.AbroadStrategy-bottom  .ht {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
}

.hidden-plan {
  background: url('../../images/strategy/plan.png') no-repeat;
}

.AbroadStrategy-bottom  .ht h2 {
  color: #fff;  
}

.AbroadStrategy-bottom  .ht p {
  color: #fff;
}

.AbroadStrategy-bottom .col-md-3:hover .ht{
  display: block;
}

.hidden-exam {
  background: url('../../images/strategy/exam.png') no-repeat;
}

.hidden-visa {
  background: url('../../images/strategy/visa.png') no-repeat;
}

.hidden-sfee {
  background: url('../../images/strategy/fee.png') no-repeat;
}

.hidden-trip {
  background: url('../../images/strategy/trip.png') no-repeat;
}

.hidden-accommodation {
  background: url('../../images/strategy/accommodation.png') no-repeat;
}

.hidden-oversea {
  background: url('../../images/strategy/oversea.png') no-repeat;
}

@media screen and ( max-width: 1300px) {
  .AbroadStrategy-top h1 {
    font-size: 0.54rem;
  }
  .AbroadStrategy-top p {
    font-size: 0.45rem;
  }
  .EntranceService-bottom {
    margin-top: 0;
  }
} 