.LearnUK-wrapper {
  position: absolute;
  display: flex;
  flex-direction: column;
  background: #f2f5fa;
  height: 100%;
  overflow-x: hidden;

  .LearnUK-body {
    padding-top: 1.46rem;
  }
  .LearnUK-body a {
    width: 100%;
    height: 500px;
  }
  
  .british-list {
    padding-top: 0.2rem;
  }
  
  .british-item {
    float: left;
    width: 25%;
    height: 5rem;
    padding: 10px 25px;
  }
  
  .climate {
    background: url('../../images/climate.png') no-repeat;
    background-size: 100% 100%;
  }
  
  .history {
    background: url('../../images/history.png') no-repeat;
    background-size: 100% 100%;
  }
  .school {
    background: url('../../images/school.png') no-repeat;
    background-size: 100% 100%;
  }
  
  .life {
    background: url('../../images/life.png') no-repeat;
    background-size: 100% 100%;  
  }
  
  
  
  
  
  .british-item h2,.british-item p {
    text-align: right;
    color: #fff;
  }
  .british-item h2 {
    font-size: 0.5rem;
  }
  .british-item p {
    font-size: 0.24rem;
  }
  
  @media screen and ( max-width: 1300px) {
    .british-item {
      height: 3.5rem;
    }
  } 
  
}

