.aboutus-wrapper {
  background:url('../../images/about_us_bg.png') #f2f5fa no-repeat;
  background-position: right top; 
  background-origin:content-box;
  background-size: 77% auto;

  // 
  .paging {
    display: flex;
    justify-content: flex-end;
    padding: 10px 0;
  }

  // _
  .shade {
    position: fixed;
    left: 0;
    bottom: 0;
    top: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    z-index: 9999;
  }

  .essay {
    width: 6rem;
    font-size: 0.2rem;
    box-shadow: 0 0 25px #ccc;

    .main {
      padding: 0.3rem 0.3rem;
      background: #fff;
      border: 1px solid #00A5D7;
      border-top: 0;
      border-bottom-left-radius: 0.1rem;
      border-bottom-right-radius: 0.1rem;
    }

    .essayList {
      height: 3rem;
      overflow-y: auto;
      margin-bottom: 0.5rem;

      &::-webkit-scrollbar {
        width: 10px;
      }
      &::-webkit-scrollbar-thumb {
        border-radius: 5px;
        background: #00A5D7;
      }
      &::-webkit-scrollbar-track {
        border-radius: 5px;
        background-color: #eee;
      }

      tr {
        cursor: pointer;
      }

      tr:hover {
        color: #00A5D7;
      }
    }

    .essayItem {
      padding: 0.1rem 0;
      border-bottom: 1px solid #00A5D7; 
      .essayTitle {
        text-align: left;
        padding-right: 1rem;
        margin: 10px 0;
      }

      .date {
        text-align: right;
        white-space: nowrap;
        margin: 10px 0;
      }
    }

    .date {
      float: right;
      padding-right: 10px;
    }

    .closeWrap {
      text-align: center;
    }

    .close_btn {
      outline: 0;
      border: 0;
      color: #fff;
      width: 1.5rem;
      height: 0.5rem;
      border-radius: 0.1rem;
      background: linear-gradient(to right bottom, #00A5D7, #5AC8EB)
    }

  }

  .head {
    padding: 0.2rem 0.3rem;
    border-top-left-radius: 0.1rem;
    border-top-right-radius: 0.1rem;
    color: #fff;
    background: linear-gradient(to right bottom, #00A5D7, #5AC8EB);
  }
}

.aboutus-banner {
  padding-top: 100px;
  width: 100%;
}

.bannerbg-top {
  padding-top: 0.3rem;
  padding-bottom: 0.3rem;
  overflow: hidden;
}

.aboutus-banner-left h1 {
  font-size: 0.82rem;
  padding-top: 0.8rem;
}

.aboutus-banner-left p {
  font-size: 0.24rem;
  color: #aaa;
}

.bannerbg-bottom ul {
  display: flex;
  justify-content: space-between;

}

.dynaInfo h2{
  font-size: 50px;
  text-align: center;
  margin: 70px 0 40px;
}

.team-intro h2 {
  font-size: 50px;
  text-align: center;
  margin: 70px 0 40px;
}

.conus h2 {
  font-size: 50px;
  text-align: center;
  margin: 70px 0 40px;
}

.cooPar h2 {
  font-size: 50px;
  text-align: center;
  margin: 70px 0 40px;
}

.founder-list  {
  display: flex;
  justify-content: space-around;
}
.founder-list .founder {
  display: inline-block;
  width: 31%;
  max-width: 460px;
  background: #fff;
  text-align: center;
  border-radius: 2%;
  padding-bottom: 0.5rem;
}
.founder img {
  width: 200px;
  height: 200px;
  margin-top: 45px;
}
.founder h3 {
  font-size: 36px;
  font-weight: 600;
  margin: 0.1rem 0 0.1rem;
  padding: 0;
}

.founder .subdesc {
  font-size: 0.2rem;
  color: #191722;
  padding: 15px 0;
}

.founder p {
  font-size: 20px;
  padding: 0 0.3rem;
  color: #aaa;
  margin: 0;

  @media screen and (max-width:1280px) {
    font-size: 16px;
  }
}



.conus-body {
  display: flex;
  justify-content: space-between;
  padding: 50px 15px 45px;
  border-radius: 10px;
  background: #fff;
  box-shadow: 0 0 10px #ccc;
}

.conus-left {
  width: 55%;
  display: inline-block;
  padding-left: 60px;
}

.conus-right {
  display: flex;
  width: 40%;
}

.qrcode-imgs {
  display: inline-block;
  text-align: center;
  margin-right: 20px;
}

.qrcode-imgs img {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  height: auto;
}

.qrcode-imgs p {
  font-size: 24px;
  padding-top: 20px;
  color: #00A5D7;
}

.conus-detail {
  overflow: hidden;
  padding-bottom: 20px;
}

.conus-detail p {
  font-size: 0.28rem;
  float: left;
}

.conus-detail span {
  display: block;
}

.conus-detail-title {
  font-weight: 600;
  width: 20%;
  width: 1rem;
}

.conus-detail-content {
  width: 80%;
  flex: auto;
  padding-left: 14px;
}

.cooPartner-body {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: #fff;
  margin-bottom: 50px;
  border-radius: 10px;
  box-shadow: 0 0 10px #ccc;
}

.cooPartner-body .rowl{
  width: 50%;
  margin: auto;
}

.cooPartner-body .rowl div{
  display: table-cell;
  width: 20%;
}

.rowl {
  text-align: center;
  &:first-child {
    padding-top: 20px;
  }
  div:first-child img {
    width: 70%;
  }
}
.rowl img {

  max-width: 100%;
  height: auto;
}



.dnyNews {
  position: relative;
  height: 500px;
  display: flex;
  justify-content: space-between;
}

.dnyNews-main {
  position: relative;
  display: inline-block;
  width: 66%;
  height: 100%;
  border-radius: 10px;
}


.dnyNews-side {
  display: inline-block;
  width: 33%;
  height: 100%;
}

.dnyNews-main .main-title {
  position: absolute;
  left: -20px;  
  bottom: 10%;
  width: 50%;
  color: #fff;
  padding-left: 0.2rem; 
  padding-top: 0.36rem;
  padding-bottom: 0.25rem;
  position: absolute;
  background: #191722;
  opacity: 0.8;
  font-size: 0.3rem;
  font-weight: 600;
  box-shadow: 0 0 8px #191722;
}

.dnyNews-main .main-date {
  font-size: 0.2rem;
  color: #aaa;
}

.dnyNews-side {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.dnyNews-side-item {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 48%;
  border-radius: 10px;
} 

.side-item-title {
  position: absolute;
  left: 0;
  padding: 0.05rem 0.1rem; 
  bottom: 0;
  width: 100%;
  color: #fff;
  background:rgba(0,0,0,0.6);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  font-size: 0.24rem;
}


.seeMore {
  position: absolute;
  right: -10%;
  top: 41%;
  display: block;
  width: 80px;
  height: 80px;
  background: url('../../images/more.png') no-repeat;
  background-size: 100%;
  line-height: 200px;
  text-align: center;
  color: #00A5D7;
}

/* second css */

.introduction {
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.introduction .intro-item{
  width: 49%;
  padding: 30px 60px 20px;
  background: #fff;
}
.intro-item {
  display: flex;
  justify-content: space-between;
  border-radius: 10px;
  box-shadow: 0 0 10px #ccc;
}

.intro-item p {
  display: inline-block;
  width: 80%;
  vertical-align: middle;
  text-indent: 30px;
  font-size: 0.22rem;
  line-height: 0.35rem;
  margin: 0;
}

.intro-item-img {
  display: flex;
  align-items: center;
  vertical-align: middle;
  width: 13.5%;
}

.xiaomengBlock {
  display: flex;
  align-items: center;
  overflow: hidden;
  background: #fff;
  padding-top: 40px;
  padding-bottom: 40px;
  padding-left: 10px;
  padding-right: 10px;
}



.intro-item-img img {
  width: 100%;
}


.block-content{
  display: flex;
  height: auto;
  flex-direction: column;
  justify-content: space-around;
}

.block-content h3 {
  text-align: right;
  font-weight: 600;
  font-size: 32px;
}

.block-content p {
  font-size: 20px;
  color: #aaa;
  line-height: 35px;
}

.block-content:last-child h3{
  text-align: left;
}

.aboutus-banner-left .about-sub-title {
  font-size: 0.45rem;
  color: #191722;
}

.aboutus-intro {
  text-indent: 50px;
  line-height: 0.38rem;
}




@media screen and (max-width: 1300px) {
  .aboutus-banner-left h1 {
    font-size: 0.7rem;
    padding-top: 0.3rem;
  }

  .aboutus-banner-left .about-sub-title {
    font-size: 0.3rem;
    padding-bottom: 5px;
  }

  .aboutus-banner-left p {
    font-size: 0.2rem;
  }

  .intro-item p {
    font-size: 0.18rem;
  }

  .block-content p {
    font-size: 0.18rem;
    line-height: 0.26rem;
  }

  .dnyNews {
    height: 3.5rem;
  }

  .dnyNews-main .main-title {
    font-size: 0.22rem;  
  }
  .conus-detail h3, .conus-detail p {
    font-size: 22px;
  }

}


@media screen and (min-width: 1300px) and (max-width: 1600px) {
  .aboutus-banner-left h1 {
    padding-top: 50px;
    padding-bottom: 15px;
  }
  .aboutus-banner-left p {
    font-size: 0.2rem;
    line-height: 0.36rem;
  }
  .intro-item p {
    font-size: 0.2rem;
  }
  .block-content p {
    line-height: 28px;
    font-size: 18px;
  }

  .aboutus-banner-left .about-sub-title {
    padding-bottom: 5px;
  }
}  