.entranceService-wrapper {
  position: absolute;
  display: flex;
  background: #f2f5fa;
  min-height: 100%;
  width: 100%;
}

.entranceService-top {
  padding-top: 1.2rem;
}

.service-top-body-left h1 {
  padding-top: 0.6rem;
  font-size: 0.54rem;
  font-weight: bold;
}

.service-top-body-left p {
  font-size: 0.48rem;
  font-weight: bold;
}

.service-top-body-left a {
  position: relative;
  padding: 0.18rem 0.5rem;
  border-radius: 0.1rem;
  font-size: 0.26rem;
  color: #fff;
  background: linear-gradient(to right, #00a5d7, #5ac8eb);
}

.service-top-body-left a:hover:after {
  position: absolute;
  left: 0;
  top: 0.8rem;
  display: block;
  content: "";
  width: 2.04rem;
  height: 2.04rem;
  background: url(../../images/xiaochengxu.png) no-repeat;
  background-size: 100% 100%;
  z-index: 100;
  box-shadow: 0 0 10px #ccc;
  border-radius: 5px;
  animation: fadeInDown 5s;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.EntranceService-bottom {
  margin-top: 0.2rem;
  background: #fff;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.EntranceService-bottom > div {
  height: 3rem;
  box-shadow: 0px 0px 2px 0px skyblue inset;
}


.EntranceService-bottom .plan {
  background: url(../../images/service/plan.png) no-repeat;
  background-size: contain;
}

.EntranceService-bottom .studyexperience {
  background: url(../../images/service/experience.png) no-repeat;
  background-size: contain;
}

.EntranceService-bottom .training {
  background: url(../../images/service/training.png) no-repeat;
  background-size: contain;
}

.EntranceService-bottom .exam {
  background: url(../../images/service/exam.png) no-repeat;
  background-size: contain;
}

.EntranceService-bottom .application {
  background: url(../../images/service/application.png) no-repeat;
  background-size: contain;
}

.EntranceService-bottom .document {
  background: url(../../images/service/document.png) no-repeat;
  background-size: contain;
}

.EntranceService-bottom .tracking {
  background: url(../../images/service/tracking.png) no-repeat;
  background-size: contain;
}

.EntranceService-bottom .visa {
  background: url(../../images/service/visa.png) no-repeat;
  background-size: contain;
}

.EntranceService-bottom .overseas {
  background: url(../../images/service/overseas.png) no-repeat;
  background-size: contain;
}
.EntranceService-bottom .col-md-3 {
  padding-left: 0;
  padding-right: 0;
  overflow: hidden;
}

.EntranceService-bottom .col-md-3 > div {
  display: none;
  width: 100%;
  height: 100%;
  padding: 0.2rem;
  background: linear-gradient(to right, #00a5d7, #5ac8eb);
}
.EntranceService-bottom .col-md-3 > div h2 {
  font-weight: 600;
  color: #fff;
  text-align: left;
}

.EntranceService-bottom .col-md-3 > div p {
  color: #fff;
  text-align: left;
}
.plan:hover .hidden-plan {
  display: block;
}
.studyexperience:hover .hidden-studyexperience {
  display: block;
}

.training:hover .hidden-training {
  display: block;
}

.application:hover .hidden-application {
  display: block;
}

.document:hover .hidden-document {
  display: block;
}

.tracking:hover .hidden-tracking {
  display: block;
}

.visa:hover .hidden-visa {
  display: block;
}

.overseas:hover .hidden-overseas {
  display: block;
}

.EntranceService-bottom .col-md-3 > div p {
  font-size: 0.24rem;
}

.EntranceService-bottom .col-md-3 p,
.EntranceService-bottom .col-md-3 h2 {
  text-align: right;
  padding-left: 0.2rem;
  padding-right: 0.2rem;
  color: #00a5d7;
}
.EntranceService-bottom .col-md-3 > h2 {
  padding-top: 20px;
  font-weight: 600;
}

.EntranceService-bottom .col-md-3 p {
  padding-top: 0;
  font-size: 18px;
}

@media screen and (max-width: 1300px) {
  .EntranceService-bottom .col-md-3 > div h2 {
    margin: 0;
  }

  .EntranceService-bottom > div {
    height: 2.4rem;
    box-shadow: 0px 0px 2px 0px skyblue inset;
  }

  .EntranceService-bottom .col-md-3 h2 {
    font-size: 0.3rem;
  }

  .EntranceService-bottom .col-md-3 p {
    font-size: 0.18rem;
  }

  .service-top-body-left h1 {
    padding-top: 0;
    padding-bottom: 0;
  }
}

@media screen and (min-width: 1350px) and (max-width: 2000px) {
  .EntranceService-bottom > div {
    height: 2.5rem;
    box-shadow: 0px 0px 2px 0px skyblue inset;
  }
}
